// Auto-load content based on current page
document.addEventListener('DOMContentLoaded', () => {
    console.log('üöÄ DOM Content Loaded event fired');
    const path = window.location.pathname;
    console.log('üìç Current path:', path);
    console.log('üîß Available loadPageContent methods:', Object.keys(window.loadPageContent));
    
    if (path.includes('about.html')) {
        console.log('‚úÖ About page detected - calling about loader');
        window.loadPageContent.about();
    } else if (path.includes('services.html')) {
        console.log('‚úÖ Services page detected - calling services loader');
        window.loadPageContent.services();
    } else if (path.includes('investment-solutions.html')) {
        console.log('‚úÖ Investment Solutions page detected - calling investment solutions loader');
        window.loadPageContent.investmentSolutions();
    } else if (path.includes('investments.html')) {
        console.log('‚úÖ Investments page detected - calling investments loader');
        window.loadPageContent.investments();
    } else {
        console.log('‚ÑπÔ∏è No matching page detected for dynamic content loading');
    }
});

// Add smooth scroll behavior for anchor links
document.addEventListener('click', (e) => {
    // Check if clicked element is a smooth scroll link
    if (e.target.matches('a.smooth-scroll') || e.target.closest('a.smooth-scroll')) {
        e.preventDefault();
        const link = e.target.matches('a.smooth-scroll') ? e.target : e.target.closest('a.smooth-scroll');
        const targetId = link.getAttribute('href');
        
        if (targetId && targetId.startsWith('#')) {
            const targetElement = document.querySelector(targetId);
            if (targetElement) {
                targetElement.scrollIntoView({
                    behavior: 'smooth',
                    block: 'start'
                });
            }
        }
    }
});

// Start Alpine
Alpine.start() 